"use client";
import { useState } from "react";
import axios from "axios";
import { toast } from "react-toastify";
const Reservation = () => {
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    phoneNumber: "",
    guests: "",
    date: "",
    time: "",
  });
  const handleSubmit = async (event) => {
    event.preventDefault();
    try {
      await axios.post("http://localhost:5000/reservations", formData, {
        headers: {
          "Content-Type": "application/json",
        },
      });
      setFormData({
        name: "",
        email: "",
        phoneNumber: "",
        guests: "",
        date: "",
        time: "",
      });
      toast.success(
        "Reservation Created Successfully. Please check your Email for confirmation."
      );
    } catch (error) {
      toast.error(error);
    }
  };
  const handleChange = (event) => {
    const { name, value } = event.target;
    setFormData((prevFormData) => ({
      ...prevFormData,
      [name]: value,
    }));
  };
  return (
    <section className='bg-[#0B1315] bg-opacity-70 py-20'>
      <div className='wrapper'>
        <form
          className='reservation-form bg-gold p-6 flex flex-col gap-2'
          onSubmit={handleSubmit}
        >
          <input
            className='block border border-solid border-black py-2 px-2'
            type='text'
            name='name'
            placeholder='Reservation name'
            value={formData.name}
            onChange={handleChange}
          />
          <input
            className='block border border-solid border-black py-2 px-2'
            type='text'
            name='email'
            placeholder='Reservation Email Address'
            value={formData.email}
            onChange={handleChange}
          />
          <input
            className='block border border-solid border-black py-2 px-2'
            type='tel'
            name='phoneNumber'
            placeholder='Reservation Phone Number'
            value={formData.phoneNumber}
            onChange={handleChange}
          />
          <input
            className='block border border-solid border-black py-2 px-2'
            type='number'
            name='guests'
            maxLength={2}
            placeholder='Reservation Guests Number'
            value={formData.guests}
            onChange={handleChange}
          />
          <input
            className='block border border-solid border-black py-2 px-2'
            type='date'
            name='date'
            value={formData.date}
            onChange={handleChange}
          />
          <input
            className='block border border-solid border-black py-2 px-2'
            type='time'
            name='time'
            value={formData.time}
            onChange={handleChange}
          />
          <button
            className='p-3 w-full max-w-[200px] font-semibold bg-white text-lightGold'
            type='submit'
          >
            BOOK NOW
          </button>
        </form>
      </div>
    </section>
  );
};
export default Reservation;
